
.verifyModalContainerBackground {
  position: absolute;
  z-index: 5;
  top: -10rem;
  width: 100vw;
  height: 105vh;
  background-color: rgba(0,0,0,0.75);

}

.verifyModalContainer {
  height: 80vh;
  width: 80vw;
  top: 7rem;
  left: 10vw;
  right: auto;
  position:absolute;
  z-index: 10;
  background: url("https://cdn.current.com/images/brochure-site/MrBeast-slide3-background.jpg");
  border-radius: .5rem;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: #fff;
  font-size: 2rem;
  animation: moveInBottom 1s linear
}

.mrBeastCircle {
  position: absolute;
  top: 5rem;
  z-index: -1;
  opacity: .33;
  width: 80%;
  max-height: 80%;
  animation: rotation 30s infinite linear
}

.verifyModalTitle {
  margin-top: 5rem;
  margin-bottom: 3rem;
}

.verifyModalContainer span {
  font-weight: bold;
}

.verifyModalContainer p {
  margin-bottom: 2rem;
  max-width: 80%;
}

.verifyModalGoBack,
.verifyModalContinue {
  margin-bottom: 2rem;
  padding: 1rem 4rem 1rem 4rem;
  font-size: 1.4rem;
  letter-spacing: -.35px;
  line-height: 1.7rem;
  border-radius: .6rem;
}

.verifyModalGoBack {
  margin-top: 5rem;
  background-color: red;
  color: #fff;
}

.verifyModalContinue {
  background-color: #82e3ff;
  color: #482a90;
}

@media (min-width: 655px) {
  .verifyModalContainerBackground{
    top: -12rem;
  }

  .verifyModalContainer {
    width: 60vw;
    height: 70vh;
    max-width: 65rem;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

@keyframes moveInBottom {
  0% {
      opacity: 0;
      transform: translateY(3rem);
  }

  100% {
      opacity: 1;
      transform: translate(0);
  }
}