
.navBar {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-right: 8rem;
  padding-left: 2rem;
  justify-content: space-between;
  height: 7rem;
}

.navBar .navMenu {
  font-size: 1.6rem;
}

.navBar .navLink {
  font-weight: bold;
}

.navSignUp {
  color: #482a90;
  background-color: #fff;
  padding-left: 1.7rem;
  padding-right: 1.7rem;
  height: 3rem;
  font-size: 1.4rem;
  letter-spacing: -.35px;
  line-height: 1.7rem;
  border-radius: .6rem;
  transition: all .3s;
}

.navBar .navSignUp:hover {
  background-color: #482a90;
  color: #fff;
}

.navBar .navHamburger {
  width: 36px;
  height: 27px;
}

.navBar .navHamburger span {
  display: block;
  height: .3rem;
  width: 100%;
  border-radius: 1px;
  opacity: 1;
  background-color: #fff;
  margin-bottom: 1rem;
}

@media (max-width: 654px) {
  .navBar {
    padding-right: 2rem;
  }
  .navLink {
     display: none;
  }
  .navSignUp {
    height: 4rem;
    font-size: 1.6rem;
    line-height: 1.8rem;
    letter-spacing: -.4px;
  }
}

@media (min-width: 655px) {
  .navLink {
      height: 100%;
      padding-left: 1.5rem;
      padding-right: 1.5rem;
  }

  .navBar {
    max-width: 75rem;
    padding-left: 3rem;
    padding-right: 3rem;
  }

  .navHamburger {
    display: none;
  }
}

@media (min-width: 1200px) {
  .navBar {
    max-width: 118rem;
  }
}

